import React, { PureComponent } from 'react';

import indexlogo from './indexlogo.svg';
import mobilehomelogo from './mobilehomelogo.svg';
import BackgroundImage from '../../components/BackgroundImage';
import Box from '../../components/Box';
import withResponsive from '../../hoc/withResponsive';

class Home extends PureComponent {
  state = {}

  componentDidMount() {
    this.setState({ isClient: typeof window !== 'undefined' })
  }

  render() {
    const { isMobile, ...props } = this.props;
    const { isClient } = this.state;

    return (
      <Box height="100%" {...props}>
        <BackgroundImage
          height="100%"
          src={isMobile && isClient ? mobilehomelogo : indexlogo}
          ratio={isMobile && isClient ? 1052.67 / 641.78 : 1083 / 1924.06}
        />
      </Box>
    );
  }
}

export default withResponsive(Home);
