import React, { PureComponent } from 'react';

import withResponsive from '../../hoc/withResponsive';
import Box from '../../components/Box';
import Text from '../../components/Text';
import Image from '../../components/Image';

const offset = '6em';
const line = '6.5em';
class SailContent extends PureComponent {
  state = {}

  componentDidMount() {
    this.setState({ isClient: typeof window !== 'undefined' })
  }

  render() {
    const {
      title,
      img,
      content,
      no,
      visible,
      isMobile,
      ...props
    } = this.props;
    const { isClient } = this.state;
    const isOdd = isMobile || no % 2;
    const ren = (
      <div>
        <Box bg="darkBlue" color="white" py="0.75em" borderRadius="1.5em 1.5em 0 0">
          <Text.h4 fontSize="1.5em" textAlign="center">
            <Text.inline color="lightYellow" mr="0.25em">{no}</Text.inline>
            {title}
          </Text.h4>
        </Box>
        <Image src={img} />
        <Box bg="milkBlue" p="1em" color="white">
          <Text textAlign="justify">{content}</Text>
        </Box>
      </div>
    );
    if (!isClient) {
      return ren;
    }
    return (
      <Box
        opacity={+Boolean(visible)}
        pointerEvents={visible ? 'auto' : 'none'}
        transition="opacity 500ms"
        {...props}
      >
        <Box
          position="absolute"
          width={line}
          left={isOdd ? `-${offset}` : 'auto'}
          right={!isOdd ? `-${offset}` : 'auto'}
          height="3px"
          bg="black"
          top="50%"
          transform="translateY(-50%)"
        />
        <Box
          width="15em"
          position="absolute"
          top="-2em"
          left={!isOdd ? offset : 'auto'}
          right={isOdd ? offset : 'auto'}
        >
          {ren}
        </Box>
      </Box>
    );
  }
}

export default withResponsive(SailContent);
